import axios from 'axios'
import moment from 'moment'
export default {
  /**
   * 删除数组中的元素
   * @param array
   * @param item
   */
  removeFromArray: (array, item) => {
    var index = array.indexOf(item)
    if (index > -1) {
      array.splice(index, 1)
    }
  },
  /**
   * 深拷贝对象
   * @param obj
   * @returns {*}
   */
  clone: function(obj) {
    let temp = null
    if (obj instanceof Array) {
      temp = obj.concat()
    } else if (obj instanceof Function) {
      // 函数是共享的是无所谓的，js也没有什么办法可以在定义后再修改函数内容
      temp = obj
    } else {
      temp = {}
      for (const item in obj) {
        const val = obj[item]
        temp[item] = typeof val === 'object' ? this.clone(val) : val // 这里也没有判断是否为函数，因为对于函数，我们将它和一般值一样处理
      }
    }
    return temp
  },
  /**
   * 时间格式化
   * @param dateObj
   * @param formatStr
   * @returns {*}
   */
  dataFormat: function(dateObj, formatStr) {
    var str = formatStr
    var Week = ['日', '一', '二', '三', '四', '五', '六']

    str = str.replace(/yyyy|YYYY/, dateObj.getFullYear())
    str = str.replace(/yy|YY/, (dateObj.getYear() % 100) > 9 ? (dateObj.getYear() % 100).toString() : '0' + (dateObj.getYear() % 100))

    str = str.replace(/MM/, dateObj.getMonth() > 9 ? dateObj.getMonth().toString() : '0' + dateObj.getMonth())
    str = str.replace(/M/g, dateObj.getMonth())

    str = str.replace(/w|W/g, Week[dateObj.getDay()])

    str = str.replace(/dd|DD/, dateObj.getDate() > 9 ? dateObj.getDate().toString() : '0' + dateObj.getDate())
    str = str.replace(/d|D/g, dateObj.getDate())

    str = str.replace(/hh|HH/, dateObj.getHours() > 9 ? dateObj.getHours().toString() : '0' + dateObj.getHours())
    str = str.replace(/h|H/g, dateObj.getHours())
    str = str.replace(/mm/, dateObj.getMinutes() > 9 ? dateObj.getMinutes().toString() : '0' + dateObj.getMinutes())
    str = str.replace(/m/g, dateObj.getMinutes())

    str = str.replace(/ss|SS/, dateObj.getSeconds() > 9 ? dateObj.getSeconds().toString() : '0' + dateObj.getSeconds())
    str = str.replace(/s|S/g, dateObj.getSeconds())

    return str
  },
  /**
   * 上传图片
   */
  uploadImg: function(file, fileName, fun) {
    axios.get('https://pro.starmerx.com/file_server/oss/getToken/').then(res => {
      var aliyunOssToken = res.data
      var formData = new FormData()
      formData.append('key', aliyunOssToken.dir + fileName)
      formData.append('OSSAccessKeyId', aliyunOssToken.accessid)
      formData.append('policy', aliyunOssToken.policy)
      formData.append('Signature', aliyunOssToken.signature)
      formData.append('file', file)
      formData.append('success_action_status', 200)
      var url = aliyunOssToken.host
      setTimeout(() => {
        fun(url)
      }, 1000)
      
      axios.post('https://starmerx.oss-cn-shanghai.aliyuncs.com', formData, { headers: { 'Content-Type': 'application/form-data' }}).then(() => {
      })
    })
  },
  /**
   * 上传图片，异步方法
   */
  uploadImgAsync(file, fileName) {
    return new Promise((resolve) => {
      axios.get('https://pro.starmerx.com/file_server/oss/getToken/').then(res => {
        const aliyunOssToken = res.data
        const formData = new FormData()
        formData.append('key', aliyunOssToken.dir + fileName)
        formData.append('OSSAccessKeyId', aliyunOssToken.accessid)
        formData.append('policy', aliyunOssToken.policy)
        formData.append('Signature', aliyunOssToken.signature)
        formData.append('file', file)
        formData.append('success_action_status', 200)
        
        axios.post('https://starmerx.oss-cn-shanghai.aliyuncs.com', formData, { headers: { 'Content-Type': 'application/form-data' }}).then((res) => {
          resolve({host: aliyunOssToken.host, aliyunRes: res})
        })
      })
    })
  },
  arrayDiff: function(base, difarr) {
    const arr = []
    base.map(x => {
      if (!difarr.includes(x)) {
        arr.push(x)
      }
    })
    return arr
  },
  // 传入图片url，返回图片尺寸
  getImageSize: function(url) {
    return new Promise((resolve) => {
      const img = new Image()
      img.src = url
      if (img.complete) {
        // 如果图片被缓存，则直接返回缓存数据
        resolve({ width: img.width, height: img.height })
      } else {
        img.onload = () => {
          resolve({ width: img.width, height: img.height })
        }
      }
    })
  },
  // 上传图片时，生成图片随机名称
  getRandomFileName(originalFileName) {
    // 时间戳
    const timeStamp = moment().unix()
    // 八位随机数
    const randomNumber = _.random(10000000, 99999999)
    // 获取后缀名
    const suffixName = originalFileName.substring(originalFileName.lastIndexOf("."), originalFileName.length)
    return `${timeStamp}${randomNumber}${suffixName}`
  }
}
